* {
  font-family: 'Space Grotesk', sans-serif;
}


body {
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

*, *:before, *:after{
  box-sizing: border-box;
}

html, body, button, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, 
p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, 
tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, 
tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video{
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

header, nav, section, main{
  display: block;
}

ol, ul{
  list-style: none;
}




